

.js-product {
  display: block;
  outline: 2 px solid black;
}

//    
.books-list {
  display: flex;
  // flex-direction: column;
  justify-content: space-between;
}

.category-container {
  position: relative; //   relative,         

  height: 625px;
}

//   
.category-container + .category-container {
  margin-top: 40px;
}

.category-style {
  color: var(--category-author);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28; /* 128.571% */
  letter-spacing: 0.42px;
  text-transform: uppercase;

  margin-bottom: 18px;
  cursor: pointer;
}

.book-name-style {
  color: var(--black);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.12; /* 112.5% */
  letter-spacing: -0.64px;
  text-transform: uppercase;

  margin-top: 14px;
  max-width: 100%;
}

.author-style {
  color: var(--category-author);
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16; /* 116.667% */
  letter-spacing: -0.48px;
}

//! ======================================== BUTTON ========================================
//    "See more"  768px
.see-more-btn {
  position: absolute;
  bottom: 0px;
  right: 0px;
  opacity: 1;
  transition: opacity 0.25s linear;
  margin-top: 8px;
}

// ,       
.resizing {
  opacity: 0; /*   0    */
}
//! ======================================== BUTTON ========================================

.img-size {
  border-radius: 8px;
  cursor: pointer;
}

@media screen and (max-width: 374px) {
  .img-size {
    width: 100%;
    height: auto;
    object-fit: cover; //*   
  }
}

@media screen and (min-width: 375px) and (max-width: 767px) {

  .category-style {
    line-height: 1.71; /* 128.571% */
  }

  //   
  .img-size {
    width: 335px;
    height: 485px;
    object-fit: cover; //*   
  }

  .book-name-style {
    width: 335px;
  }

  .author-style {
    width: 335px;
  }
}

//        
@media screen and (min-width: 768px) and (max-width: 1439px) {

  .cover {
    display: flex;
  }

  .category-container {
    width: 704px;
    height: 474px;
  }

  .img-size {
    width: 218px;
    height: 316px;
  }

  .book-name-style {
    width: 218px;
  }

  .author-style {
    width: 218px;
  }
}

@media screen and (min-width: 1440px) {

  .cover {
    flex-direction: column;
  }

  .category-container {
    width: 996px;
    height: 426px;
  }

  .img-size {
    width: 180px;
    height: 256px;
  }

  .book-name-style {
    width: 180px;
  }

  .author-style {
    width: 180px;
  }
}
